import App from './xpage/core';
import domReady from './xpage/ready';
import "./textPage/text-page";
import "./forms";
import MobileMenu from './xpage/mobileMenu';
import EventListener from './xpage/EventListener';
import select from './xpage/select';


declare global {
	interface Window {
		$: any;
		is: any;
		sectorSliderInit: Function,
		moveInfoMerch: Function,
		getImageFromHTML: Function,
		animation: Function
	}

	interface Set<T> {
		getByIndex: Function
	}
}

if (window.is.safari())
	document.querySelector("html")?.classList.add("bx-safari")



window.animation = () => {	
	const options = {
		rootMargin: "0px",
			thresold: .7
		};
	
	function callback(entries: Array<IntersectionObserverEntry>, observer: IntersectionObserver){
		entries.map((entry: IntersectionObserverEntry, i: number) => {
			if (entry.isIntersecting){
				entry.target.classList.add("js__active");
				
				
			}
		})
	};
	
	const obs = new IntersectionObserver(callback, options);

	App.each(".fade-up, .news__item-cont, .programs__item-cont, .gallery__item-cont, .video__item-cont, .video__item-cont--main", function(el: HTMLElement){
		obs.observe(el);
	})
	
}
	
	
window.addEventListener("load", function(){
	
	window.animation();
	
})

domReady(() => {

	/**
	 * скрытие колонок через css, если в них нет потомков
	 */
	document.querySelectorAll(".champ-grid__column").forEach(
		item => item.innerHTML = item.children.length ? item.innerHTML : ""
	);

	if(!document.querySelector('#bx-panel-top'))
		window.$("body").on("mousedown", "select:not(.no-my-select)", function(e: Event){
			const $this = $(this)
		
			if (window.is.touchDevice()) return;
		
			e.preventDefault();
			
			if (this.classList.contains("my-select"))
				$this.closest("div").find(".my-select__list-cont").remove();
			
			new select(this);
		
			this.classList.add("my-select");
			this.closest("div").style.position = "relative";
		
			$this.addClass("js__opened");
		});

	new MobileMenu({
		burger: ".main-aside__burger",
		menu: ".popup-menu__container",
		menuActiveClass: "js__opened",
		bodyActiveClass: "js__menu-open",
		ignoreWarnings: false,
		fixBody: true,
		media: "screen"
	});	


	if(window.matchMedia("(max-width: 1000px)").matches){
		new EventListener(".p-menu__link").add("click", (el: HTMLElement, e: Event) => {
			const parent = el.closest(".p-menu__item"),
				submenu = parent.querySelector(".p-menu__submenu");
	
			if (submenu)
				e.preventDefault();
	
			submenu?.classList?.add("js__opened");
		});

	}


	new EventListener(".p-menu__submenu-back").add("click", (el: HTMLElement) => {
		const targetSubmenu = el.closest(".p-menu__submenu");

		targetSubmenu?.classList?.remove("js__opened");
	});
});


